.dataTables_wrapper .dataTables_length label {
  display: flex;
  align-items: center;
  column-gap: 4px;
}
.dataTables_wrapper .dataTable thead tr th {
  background-color: #222;
  color: #fff;
  transition: all 0.5s;
}
.dataTables_wrapper .dataTable thead tr th:hover {
  background-color: #333;
}
.dataTables_wrapper .dataTable thead tr th::before, .dataTables_wrapper .dataTable thead tr th:after {
  color: #fff !important;
  opacity: 0.6 !important;
}
.dataTables_wrapper .dataTable thead tr th.sorting_asc, .dataTables_wrapper .dataTable thead tr th.sorting_desc {
  background-color: #000;
}
.dataTables_wrapper .dataTable thead tr th.sorting_asc::before {
  opacity: 0.9 !important;
}
.dataTables_wrapper .dataTable thead tr th.sorting_desc::after {
  opacity: 0.9 !important;
}
.dataTables_wrapper .dataTable tbody .Activo, .dataTables_wrapper .dataTable tbody .Activa {
  color: #30bf30;
  font-weight: 700;
}
.dataTables_wrapper .dataTable tbody .Inactivo, .dataTables_wrapper .dataTable tbody .Inactiva {
  color: red;
  font-weight: 700;
}
.dataTables_wrapper .dataTable tbody .acciones .accion {
  background-color: #0095e8;
  color: #fff;
  font-size: 13px;
  text-transform: uppercase;
  padding: 8px 18px;
  line-height: 1;
  font-weight: 600;
  border: none;
  transition: all 0.5s;
}
.dataTables_wrapper .dataTable tbody .acciones .accion:hover {
  background-color: #00d6b0;
  color: #2d2d2d;
}

#wrap-usuarios {
  display: flex;
  flex-wrap: wrap;
  row-gap: 20px;
}
#wrap-usuarios .wrap-listado-usuarios,
#wrap-usuarios .wrap-gestion-usuarios {
  width: 100%;
}
#wrap-usuarios .wrap-gestion-usuarios {
  display: none;
}